// Colors
// ---------------------------------

// Default

$color-default-black:               #484848;
$color-default-white:               #ffffff;
$color-transparent-white:                 rgba(255, 255, 255, 0.7);
$color-transparent-white-line:                 rgba(255, 255, 255, 0.2);
$color-transparent-white-select:                 rgba(255, 255, 255, 0.3);
$color-transparent-white-sub-link: rgba(255, 255, 255, 0.6);
$color-transparent-blue:        rgba(49, 109, 194, 0.7);
$color-transparent-blue-default: rgba(49, 109, 194, 0.5);
$color-transparent-blue-tabs: rgba(49, 109, 194, 0.5);
$color-transparent-blue-scrollbar: rgba(49, 109, 194, 0.2);
$color-background-error: rgba(255, 94, 102, 0.2);
$color-transparent-black: rgba(72, 72, 72, 0.3);

// Project palette
// для нейминга цветов используем https://www.htmlcsscolor.com/hex/334482

$color-neon-blue:                   #2c39f2;
$color-wild-watermelon:             #ff5e66;
$color-alabaster:                   #f0efe9;
$color-cerulean-blue:               #316dc2;
$color-endeavour:                   #275699;
$color-mischka:                     #a9afb7;
$color-photo-overlay:             rgba(49, 109, 194, 0.2);
$color-menu-form-overlay:         rgba(13, 29, 51, 0.4);
// Gradient

$black-to-right:                    linear-gradient(90deg, rgba(0, 0, 0, 0.75) 0%, rgba(0, 0, 0, 0) 75%);

// Typography
// ---------------------------------

// переменная используемая в html для подключения скейлинга
$fz-default:                        14px;

// у некоторых шрифтов в ios возникают проблемы с символом рубля
// https://www.artlebedev.ru/kovodstvo/sections/159/#13
$ff-rouble:                         "Rouble", "Arial", sans-serif;

$ff-manrope:                       "Manrope", "Arial", sans-serif;

// Animation
// ---------------------------------

$tf-default:                        ease;
$trans-default:                     0.3s $tf-default;

// если требуется изинг отличный от $tf-default, то переменную называем не цифрой
$trans-modal:                       0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-600:                         0.6s $tf-default;

// Viewports
// ---------------------------------

$vp-320:                            320px;
$vp-768:                            768px;
$vp-1200:                           1200px;

// Retina
// ---------------------------------

$retina-dpi:                        144dpi;
$retina-dppx:                       1.5dppx;
