.custom-select {
  display: flex;
  flex-direction: column;
  position: relative;
  gap: 12px;

  width: 100%;
  padding: 0;
  margin: 0;
}

.custom-select__placeholder {
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;

  color: $color-default-black;
  opacity: 0.8;
}

.custom-select select {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;

  white-space: nowrap;

  border: 0;

  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.custom-select__list {
  position: absolute;
  bottom: 4px;
  z-index: 1;

  width: 100%;
  min-height: 192px;
  margin: 0;
  padding: 0;
  overflow-y: auto;

  list-style: none;

  background-color: $color-default-white;
  border: 1px solid $color-default-black;
  border-radius: 8px;
  transform: translateY(100%);
  visibility: hidden;
  opacity: 0;

  transition: visibility 0.3s ease, opacity 0.3s ease;
}

.custom-select__text {
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
  flex-shrink: 0;
  width: 80%;
  overflow: hidden;

  color: $color-default-black;
  text-align: left;
  white-space: nowrap;
  text-overflow: ellipsis;

  opacity: 0;

  transition: opacity 0.3s ease 0.1s;
}

.custom-select__icon {
  flex-shrink: 0;
  width: 16px;
  height: 8px;
  margin-left: auto;
  margin-right: -2px;

  background-color: $color-default-black;

  transition: transform 0.3s ease;

  clip-path: polygon(100% 0, 100% 20%, 50% 100%, 0 20%, 0 0, 50% 80%);
}

.custom-select.is-disabled {
  pointer-events: none;

  .custom-select__list {
    display: none;
  }
}

.custom-select.is-open {
  .custom-select__list {
    visibility: visible;
    opacity: 1;
  }

  .custom-select__icon {
    transform: rotate(180deg);
  }

  .custom-select__label {
    opacity: 0;
  }
}

.custom-select.not-empty {
  .custom-select__text {
    opacity: 1;
  }

  .custom-select__label {
    opacity: 0;
  }
}

.custom-select--dark-bg {
  width: 350px;
  padding: 0;
  margin: 0;

  @include vp-1199 {
    gap: 7px;
  }

  @include vp-767 {
    width: 100%;
    gap: 10px;
  }

  @include hover {
    .custom-select__button--dark-bg {
      border: 1px solid $color-default-white;
    }
  }

  @include focus {
    .custom-select__button--dark-bg {
      border: 1px solid $color-default-white;
    }
  }

  @include active {
    .custom-select__button--dark-bg {
      border: 1px solid $color-default-white;
    }
  }
}

.custom-select--dark-bg.is-open .custom-select__button--dark-bg {
  border: 1px solid $color-default-white;
}

.custom-select__button {
  display: flex;
  align-items: center;
  width: 100%;
  min-height: 48px;
  padding: 16px 19px;
  background-color: transparent;
  border: 1px solid $color-transparent-black;

  border-radius: 5px;
  outline: none;
  cursor: pointer;

  transition: border 0.3s ease;
}

.custom-select__button--dark-bg {
  border: 1px solid $color-transparent-white-select;
  border-radius: 8px;
  padding: 16px 19px;

  @include vp-767 {
    min-height: 50px;
    padding: 16px 19px 18px 19px;
  }
}

.custom-select__text--dark-bg {
  color: $color-alabaster;
}

.custom-select__icon--dark-bg {
  background-color: $color-alabaster;
}

.custom-select__list--dark-bg {
  border-radius: 8px;
  background-color: $color-default-white;
  border: none;
  padding: 1px;
  bottom: -4px;
  max-height: 192px;
}

.custom-select__item {
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
  color: $color-default-black;
  padding: 17px 20px;

  outline: none;
  cursor: pointer;

  transition: background-color 0.3s ease;
}

.custom-select__item[aria-selected="true"] {
  background-color: $color-cerulean-blue;
  color: $color-default-white;
}

.custom-select__item--dark-bg {
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
  color: $color-default-black;
  padding: 17px 20px;
}

.custom-select span {
  @include vp-767 {
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 14px;
    color: $color-default-white;
    opacity: 0.8;
  }
}

.custom-select.is-invalid {
  .custom-select__button {
    border: 1px solid $color-wild-watermelon;
    background-color: $color-background-error;
  }

  .custom-select__icon {
    background-color: $color-wild-watermelon;
  }
}

.custom-select__placeholder--modal {
  color: $color-default-black;
}

.custom-select span {
  @include vp-767 {
    color: $color-default-black;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px;
  }
}

.custom-select--dark-bg span {
  @include vp-767 {
    color: $color-alabaster;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 14px;
  }
}

.custom-select--modal {
  @include vp-767 {
    gap: 6px;
  }
}

.custom-select--modal .custom-select__button {
  @include vp-767 {
    min-height: 44px;
    padding: 12px 19px;
  }
}
