.contacts {
  width: 100%;
  background-color: $color-cerulean-blue;
  color: $color-default-white;
}

.contacts__wrapper {
  margin: 0 auto;
  padding: 119px 0 120px 0;

  max-width: 1240px;

  display: grid;
  grid-auto-flow: dense row;
  grid-template-columns: 223px 1fr;
  gap: 32px;

  @include vp-1199 {
    min-width: 678px;
    max-width: 678px;
    display: flex;
    flex-direction: column;
    gap: 25px;

    padding: 100px 0 99px 0;
  }

  @include vp-767 {
    min-width: 100%;
    max-width: 100%;
    padding: 69px 0 67px 0;

    gap: 24px;
  }
}

.contacts__wrapper h2 {
  font-size: 50px;
  font-style: normal;
  font-weight: 400;
  line-height: 50px;
  color: $color-default-white;
  word-break: break-all;

  padding: 6px 0 0 0;

  margin: 0;

  @include vp-1199 {
    align-self: self-start;
    padding: 0;

    font-size: 34px;
  }

  @include vp-767 {
    font-size: 20px;
    line-height: 20px;
    margin: 0 0 7px 0;
  }
}

.contacts__wrapper dl {
  display: flex;
  flex-wrap: wrap;
  gap: 52px;

  margin: 0 0 0 auto;

  @include vp-1199 {
    gap: 88px;
    margin: 0;
  }

  @include vp-767 {
    flex-direction: column;
    gap: 14px;
  }
}

.contacts__info-wrapper {
  display: flex;
  flex-direction: column;
  max-width: 268px;

  gap: 15px;

  @include vp-1199 {
    gap: 19px;
  }

  @include vp-767 {
    max-width: 235px;
    gap: 8px;
  }
}

.contacts__info-wrapper dt {
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 12px;
  letter-spacing: 0.12px;
  text-transform: uppercase;
  word-break: break-all;

  color: $color-alabaster;
}

.contacts__info-wrapper dd {
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 26px;
  word-break: break-all;

  color: $color-default-white;

  margin: 0;

  @include vp-1199 {
    line-height: 22px;
  }

  @include vp-767 {
    font-size: 14px;
    line-height: 20px;
  }
}

.contacts__info-wrapper a {
  color: $color-default-white;
  padding: 0 0 5px 0;

  @include hover-focus {
    border-bottom: 1px solid $color-default-white;
  }

  @include active {
    opacity: 0.8;
  }
}
